import { useCallback, useEffect, useState } from "react";
import { PageLayout } from "../../common";
import { ProjectItem } from "./ProjectItem";
import { ProjectModalContainer as ProjectModal } from "./Modal/ProjectModalContainer";
import * as S from "./styles";

import { ProjectStoreImpl } from "../../stores/ProjectStore";

function useDelayUnmount(isMounted: boolean, delayTime: number) {
  const [shouldRender, setShouldRender] = useState(false);

  useEffect(() => {
    let timeoutId: NodeJS.Timeout;

    if (isMounted && !shouldRender) {
      setShouldRender(true);
    } else if (!isMounted && shouldRender) {
      timeoutId = setTimeout(() => setShouldRender(false), delayTime);
    }
    return () => clearTimeout(timeoutId);
  }, [isMounted, delayTime, shouldRender]);

  return shouldRender;
}

export const ProjectContainer = () => {
  const [clickIndex, setClickIndex] = useState(0);
  const [isMounted, setIsMounted] = useState(false);
  const shouldRenderChild = useDelayUnmount(isMounted, 600);

  const handleClick = useCallback((index: number) => {
    setClickIndex(index);
    setIsMounted(true);
  }, []);

  const handleCloseModal = useCallback(() => {
    setIsMounted(false);
  }, []);

  return (
    <PageLayout title="Project">
      <S.ProjectList>
        {ProjectStoreImpl.projectList?.map(
          ({ imgUrl, title, tags, bgColor }, index) => (
            <ProjectItem
              imgUrl={imgUrl}
              bgColor={bgColor}
              title={title}
              tags={tags}
              handleClick={() => handleClick(index)}
            />
          )
        )}
      </S.ProjectList>
      {shouldRenderChild && (
        <ProjectModal
          isMounted={isMounted}
          clickIndex={clickIndex}
          handleClose={handleCloseModal}
        />
      )}
    </PageLayout>
  );
};
